@import '../../styles/_variables';
@import '../../styles/_mixins';

.search-bar {
  position: relative;
  width: 0%;
  min-width: 32px;
  height: 32px;
  border-radius: 5000px;
  background-color: transparent;
  box-shadow: none;
  border: 1px solid transparent;
  float: left;
  overflow: hidden;
  -webkit-transition: width 0.3s ease;
  -moz-transition: width 0.3s ease;
  transition: width 0.3s ease;
  -webkit-backface-visibility: hidden;
  &.is-expanded {
    width: 100%;
    max-width: 360px;
    border: 1px solid $grey-400;
    .search-form__btn {
      z-index: 11;
    }
    .search-form__submit {
      z-index: 90;
    }
    .search-form__input {
      background-color: transparent;
    }
    .search-form__btn {
      color: $primary;
    }
  }
}

.search-form {
  width: 100%;
}

.search-form__input {
  position: absolute;
  top: 0;
  left: 32px;
  border: none;
  outline: none;
  background-color: transparent;
  font-size: 14px;
  padding: 4px 4px 4px 0;
  height: 32px;
  width: calc(100% - 36px);
  vertical-align: middle;
  z-index: 10;
}

input[type='search'].search-form__input {
  -webkit-appearance: none;
  -webkit-border-radius: 0px;
}

.search-form__btn,
.search-form__submit {
  width: 30px;
  height: 30px;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  padding: 0;
  margin: 0;
  line-height: 0;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
}

.search-form__submit {
  background-color: $white;
  opacity: 0;
  color: transparent;
  border: none;
  outline: none;
  z-index: -1;
}

.search-form__btn {
  z-index: 90;
  color: $grey-700;
  .feather {
    width: 20px;
    height: 20px;
    vertical-align: middle;
  }
}
